
Î».force_touch = {};

/* Begin Image zoom JS */
Î».force_touch.x_start,
Î».force_touch.x_current,
Î».force_touch.y_start,
Î».force_touch.y_current;


Î».force_touch.image_zoom_onTouchStart = function(event) {
	Î».force_touch.currentClickEl = event.target;
	event.preventDefault();
	Î».force_touch.image_zoom_checkForce(event);

	Î».force_touch.x_start = event.clientX || event.pageX;
	Î».force_touch.y_start = event.clientY || event.pageX;
};

Î».force_touch.image_zoom_onTouchMove = function(event) {
	Î».force_touch.image_zoom_checkForce(event);
	Î».force_touch.x_current = event.clientX || event.pageX;
	Î».force_touch.y_current = event.clientY || event.pageY;
};

Î».force_touch.image_zoom_onDragStart = function(event) {
	event.preventDefault();
};

Î».force_touch.image_zoom_onTouchEnd = function(event) {
	touch = null;
	$(currentClickEl).removeAttr('style');
	clearTimeout(Î».force_touch.currentClickTimeout);
	Î».force_touch.currentClickTimeout = currentClickEl = Î».force_touch.x_start = Î».force_touch.x_current = Î».force_touch.y_start = Î».force_touch.y_current = null;
};

Î».force_touch.image_zoom_checkForce = function(event) {
	touch = event.touches[0];
	Î».force_touch.currentClickTimeout = setTimeout(Î».force_touch.image_zoom_refreshForceValue.bind(touch), 2);
};

Î».force_touch.image_zoom_checkMacForceBegin = function(event) {
	/* max value for trackpad is 3.0 compare to 1.0 on iOS */
	Î».force_touch.currentClickEl = event.target;

	Î».force_touch.x_start = event.clientX || event.pageX;
	Î».force_touch.y_start = event.clientY || event.pageY;

	Î».force_touch.image_zoom_renderElement(event.webkitForce / 3);
};

Î».force_touch.image_zoom_checkMacForce = function(event) {
	/* max value for trackpad is 3.0 compare to 1.0 on iOS */
	Î».force_touch.x_current = event.clientX || event.pageX;
	Î».force_touch.y_current = event.clientY || event.pageY;

	Î».force_touch.image_zoom_renderElement(event.webkitForce / 3);
};

Î».force_touch.image_zoom_refreshForceValue = function() {
	var touchEvent = this;
	var forceValue = 0;
	if (touchEvent) {
		forceValue = touchEvent.force || 0;
		if (forceValue !== 0) {
			forceValue = forceValue;
			currentClickTimeout = setTimeout(Î».force_touch.image_zoom_refreshForceValue.bind(touch), 2);
		}
	} else {
		forceValue = 0;
	}

	Î».force_touch.image_zoom_renderElement(forceValue);
};

Î».force_touch.image_zoom_renderElement = function(forceValue) {

	var scale = Math.clip((1 + forceValue), 1, 2.5);

	// var y_percent = (y_start / y_current) * 100;

	if (Î».force_touch.x_current !== "undefined" || Î».force_touch.x_current !== null) {
		var x_percent = Math.clip(( Î».force_touch.x_start - Î».force_touch.x_current ), -50, 50);
		$(Î».force_touch.currentClickEl).css({'transform': 'translateX(' + x_percent + '%) scale(' + scale + ')'});
	} else {
		$(Î».force_touch.currentClickEl).css({'transform': 'scale(' + scale + ')'});
	}
};

Î».force_touch.currentClickEl;

Î».force_touch.currentClickTimeout;

Î».force_touch.rebind_3dtouch_image_zoom = function(el) {
	el.addEventListener('touchstart', Î».force_touch.image_zoom_onTouchStart, false);
	el.addEventListener('touchmove', Î».force_touch.image_zoom_onTouchMove, false);
	el.addEventListener('touchend', Î».force_touch.image_zoom_onTouchEnd, false);
	el.addEventListener('mouseup', Î».force_touch.image_zoom_onTouchEnd, false);
	el.addEventListener('dragstart', Î».force_touch.image_zoom_onDragStart, false);
	el.addEventListener('webkitmouseforcewillbegin', Î».force_touch.image_zoom_checkMacForceBegin, false);
	el.addEventListener('webkitmouseforcechanged', Î».force_touch.image_zoom_checkMacForce, false);
	el.addEventListener('mouseout', Î».force_touch.image_zoom_onTouchEnd, false);
};

window.unbind_3dtouch_image_zoom = function(el) {
	el.removeEventListener('touchstart', Î».force_touch.image_zoom_onTouchStart, false);
	el.removeEventListener('touchmove', Î».force_touch.image_zoom_onTouchMove, false);
	el.removeEventListener('touchend', Î».force_touch.image_zoom_onTouchEnd, false);
	el.removeEventListener('mouseup', Î».force_touch.image_zoom_onTouchEnd, false);
	el.removeEventListener('dragstart', Î».force_touch.image_zoom_onDragStart, false);
	el.removeEventListener('webkitmouseforcewillbegin', Î».force_touch.image_zoom_checkMacForceBegin, false);
	el.removeEventListener('webkitmouseforcechanged', Î».force_touch.image_zoom_checkMacForce, false);
	el.removeEventListener('mouseout', Î».force_touch.image_zoom_onTouchEnd, false);
};

